import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Typography, Card, CardActions, CardActionArea, CardContent, createStyles } from '@material-ui/core';
import { Link as GatsbyLink } from 'gatsby';
import Img from 'gatsby-image';

const useStyles = makeStyles((theme) => 
  createStyles({
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  learnMore: {
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(1),
  }
}));

export default function OutlinedCard(props) {
  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <CardActionArea href={props.link}>
        {props.image ? <Img fluid={props.image.childImageSharp.fluid} /> : null}
        <CardContent>
          <Typography variant='h5'>
            {props.title}
          </Typography>
          <Typography variant="body2" component="p">
            {props.body}
          </Typography>
        </CardContent>
        <CardActions>
        <div className={classes.learnMore}>Learn More</div>
        </CardActions>
      </CardActionArea>
    </Card>
  );
}
